.form_container{
    max-width: 300px;
    min-height: 400px;
    margin: 20px auto;
    text-align: center;
    padding: 15px 0 0 0;
    border: 2px solid #fe6637;
    box-shadow: 3px 2px 35px 8px rgba(242,58,58,0.97);
    border-radius: 10%;
}

.form_header{
    text-transform: uppercase;
    font-size: 23px;

}

.input_container{
    max-width: 200px;
    margin: auto;
}

.rememberMe{
    margin: 15px 0 0 0; 
    text-align: left;
    padding: 0 0px;
}

.rememberMe_label{
    display: inline-block;
    text-transform: uppercase;
    font-size: 12px;
    margin: 15px;
}

.rememberMe_checkbox{
    display: inline-block;
}


.form_field{
    margin: 15px 0 0 0;
}

.general_error{
    display: block;
    max-width: 100%;
    height: calc(1.5rem + 2px);
    padding: 0.375rem 0.75rem;
    font-family: inherit;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #212529;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid red;
    border-radius: 0.25rem;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.shine_button {
    text-decoration: none;
    display: inline-block;
    padding: 10px 30px;
    margin: 10px 20px;
    position: relative;
    overflow: hidden;
    border: 2px solid #fe6637;
    border-radius: 8px;
    font-family: 'Montserrat', sans-serif;
    color: #fe6637;
    transition: .2s ease-in-out;
  }
  .shine_button:before {
    content: "";
    background: linear-gradient(90deg, rgba(255, 255, 255, .1), rgba(255, 255, 255, .5));
    height: 50px;
    width: 50px;
    position: absolute;
    top: -8px;
    left: -75px;
    transform: skewX(-45deg);
  }
  .shine_button:hover {
    background: #fe6637;
    color: #fff;
  }
  .shine_button:hover:before {
    left: 150px;
    transition: .5s ease-in-out;
  }