* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
  width: 100%;
}

#root{
  min-height:100vh;
}

.App{
  min-height:100vh;
  width: 100%;
}

p{
  margin: 0;
}

.wrapper{
  max-width: 1300px;
  min-height: 100vh;
  margin: 0 auto;
  display: grid;
  grid-template-rows: 50px 1fr 50px;
  grid-template-columns: 1.5fr 10fr;
  grid-gap: 10px;
  grid-template-areas:
  'header header'
  'sidebar content'
  'footer footer';
}

.content{
  grid-area: content;
  min-height: 100%;
}

.footer{
  color: blue;
}




