.dialogs_page{
    margin: 20px 0 0 0;
    height: 100vh;
    display: grid;
    grid-template-areas: 
    'users messages'
    'input_message input_message';
    grid-template-rows: 1fr auto;
    grid-template-columns: 1fr 3.5fr;
}

.users{
    grid-area: users;
    border-right: 1px black solid;
}

.messages{
    grid-area: messages;
}

.input_message{
    margin: 30px 0 30px 0;
    grid-area: input_message;
}

.dialogs_form{
    display: flex;
    align-items: center;
}

.dialogs_form textarea{
    margin: 0 20px 0 0;
    width: 90%;
    height: 100%;
}

.dialog_item{
    height: 50px;
    width: 130px;
    text-align: left;
    text-decoration: none;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    border: 1px black solid;
    border-radius: 20px 20px 20px 20px;
    padding: 0 0 0 10px;
    margin: 0 0 5px 0;
}

.dialog_item a{
    text-decoration: none;
}

.dialog_item a:visited{
    color: black;
}

.dialog_item img{
    max-width: 30px;
    max-height: 30px;
    margin: 0 10px 0 0;
}


.message_item{
    height: 50px;
    width: 100%;
    text-align: left;
    display: flex;
    align-items: center;
    margin: 0 0 5px 0;
    border-bottom: 1px solid black;
    padding: 0 0 0 20px;
}

 
